<template>
  <transition-group name="flip-list" tag="div" class="posts" @enter="shuffle()" appear>
    <div
      class="post"
      :class="getSize()"
      v-for="(post, i) in places"
      :key="`post-${i}`"
      :style="getStyles(i)"
    >
      <router-link
        class="overlay center"
        tag="div"
        :to="{ path: `/posts/${places[i]}`, query: { bg: backgrounds[i] }}"
      >{{ post }}</router-link>
    </div>
  </transition-group>
</template>

<script>
import { shuffle } from "lodash";

export default {
  name: "Posts",
  data() {
    return {
      postCount: 20,
      places: [
        "Ommen",
        "Emmen",
        "Hardenberg",
        "Groningen",
        "Utrecht",
        "Almelo",
        "Enschede",
        "Amsterdam",
        "Eindhoven",
        "Leeuwarden",
        "Meppel",
        "Winschoten",
        "Zwolle",
        "Gieten",
        "Assen",
        "Tilburg",
        "Maassluis",
        "Hengelo",
        "Slagharen",
        "Coevorden"
      ],
      backgrounds: []
    };
  },
  methods: {
    shuffle: function() {
      setTimeout(() => {
        this.places = shuffle(this.places);
      }, 150);
    },
    getStyles(i) {
      let url = `https://source.unsplash.com/800x6${i + 10}/?people`;

      let r = { backgroundImage: `url(${url})` };
      this.backgrounds[i] = url;
      return r;
    },
    getSize() {
      let r = [{ "span-1": true }, { "span-2": true }, { "span-3": true }][
        Math.floor(Math.random() * 3)
      ];
      return r;
    }
  }
};
</script>

<style lang="scss" scoped>
.posts {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  grid-gap: 10px;
  grid-auto-rows: minmax(120px, auto);
  grid-auto-flow: dense;
  grid-gap: 10px;
  padding: 10px;
  overflow: scroll;

  .post {
    object-fit: contain;
    cursor: pointer;
    background-position: center;
    transition: filter background-position 0.3s ease-in-out;
    position: relative;

    .overlay {
      height: 100%;
      width: 100%;
      background-color: #000;
      opacity: 0;
      position: absolute;
      bottom: 0;
      left: 0;
      height: 0;
      width: 100%;
      transition: all 0.3s ease-out;
      filter: blur(0px);
    }

    &:hover {
      filter: blur(2px);
      background-position: 50% 80%;

      .overlay {
        opacity: 1;
        height: 25%;
        color: #fff;
        font-size: var(--font-xl);
        text-transform: uppercase;
      }
    }
    &.span-1 {
      grid-column-end: span 1;
      grid-row-end: span 1;
    }
    &.span-2 {
      grid-column-end: span 2;
      grid-row-end: span 2;
    }
    &.span-3 {
      grid-column-end: span 3;
      grid-row-end: span 4;
    }
  }
}
.flip-list-move {
  transition: transform 1s;
}
</style>